import React from 'react';
import { motion } from 'framer-motion';

// Blockchain Network Animation
export const BlockchainNetworkSVG: React.FC<{ className?: string }> = ({ className }) => {
  return (
    <motion.svg
      className={className}
      viewBox="0 0 400 300"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ duration: 1 }}
    >
      {/* Network nodes */}
      {[
        { x: 80, y: 80, delay: 0 },
        { x: 320, y: 80, delay: 0.3 },
        { x: 200, y: 150, delay: 0.6 },
        { x: 80, y: 220, delay: 0.9 },
        { x: 320, y: 220, delay: 1.2 }
      ].map((node, index) => (
        <motion.circle
          key={index}
          cx={node.x}
          cy={node.y}
          r="8"
          fill="#3B82F6"
          initial={{ scale: 0 }}
          animate={{ scale: [0, 1.2, 1] }}
          transition={{
            duration: 0.8,
            delay: node.delay,
            repeat: Infinity,
            repeatDelay: 2
          }}
        />
      ))}
      
      {/* Connecting lines */}
      {[
        { x1: 80, y1: 80, x2: 320, y2: 80 },
        { x1: 80, y1: 80, x2: 200, y2: 150 },
        { x1: 320, y1: 80, x2: 200, y2: 150 },
        { x1: 200, y1: 150, x2: 80, y2: 220 },
        { x1: 200, y1: 150, x2: 320, y2: 220 }
      ].map((line, index) => (
        <motion.line
          key={index}
          x1={line.x1}
          y1={line.y1}
          x2={line.x2}
          y2={line.y2}
          stroke="#3B82F6"
          strokeWidth="2"
          initial={{ pathLength: 0 }}
          animate={{ pathLength: 1 }}
          transition={{
            duration: 1.5,
            delay: index * 0.2,
            repeat: Infinity,
            repeatDelay: 3
          }}
        />
      ))}
      
      {/* Data packets */}
      <motion.circle
        cx={80}
        cy={80}
        r="4"
        fill="#10B981"
        animate={{
          cx: [80, 200, 320, 200, 80],
          cy: [80, 150, 80, 150, 80]
        }}
        transition={{
          duration: 4,
          repeat: Infinity,
          ease: "linear"
        }}
      />
    </motion.svg>
  );
};

// Stock Chart Animation
export const StockChartSVG: React.FC<{ className?: string }> = ({ className }) => {
  const chartData = [
    { x: 50, y: 200 },
    { x: 100, y: 180 },
    { x: 150, y: 160 },
    { x: 200, y: 140 },
    { x: 250, y: 120 },
    { x: 300, y: 100 },
    { x: 350, y: 80 }
  ];

  return (
    <motion.svg
      className={className}
      viewBox="0 0 400 250"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ duration: 1 }}
    >
      {/* Chart background grid */}
      {[0, 1, 2, 3, 4].map((line) => (
        <motion.line
          key={line}
          x1="50"
          y1={50 + line * 40}
          x2="350"
          y2={50 + line * 40}
          stroke="#374151"
          strokeWidth="1"
          opacity="0.3"
          initial={{ pathLength: 0 }}
          animate={{ pathLength: 1 }}
          transition={{ duration: 0.5, delay: line * 0.1 }}
        />
      ))}
      
      {/* Chart line */}
      <motion.path
        d={`M ${chartData.map(point => `${point.x},${point.y}`).join(' L ')}`}
        stroke="#10B981"
        strokeWidth="3"
        fill="none"
        initial={{ pathLength: 0 }}
        animate={{ pathLength: 1 }}
        transition={{ duration: 2, delay: 0.5 }}
      />
      
      {/* Data points */}
      {chartData.map((point, index) => (
        <motion.circle
          key={index}
          cx={point.x}
          cy={point.y}
          r="4"
          fill="#10B981"
          initial={{ scale: 0 }}
          animate={{ scale: [0, 1.3, 1] }}
          transition={{
            duration: 0.5,
            delay: 0.5 + index * 0.2
          }}
        />
      ))}
      
      {/* Price indicator */}
      <motion.text
        x="300"
        y="70"
        fill="#10B981"
        fontSize="14"
        fontWeight="bold"
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ duration: 0.5, delay: 2 }}
      >
        +15.4%
      </motion.text>
    </motion.svg>
  );
};

// Threat Detection Animation
export const ThreatDetectionSVG: React.FC<{ className?: string }> = ({ className }) => {
  return (
    <motion.svg
      className={className}
      viewBox="0 0 400 300"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ duration: 1 }}
    >
      {/* Radar circles */}
      {[60, 100, 140].map((radius, index) => (
        <motion.circle
          key={index}
          cx="200"
          cy="150"
          r={radius}
          stroke="#3B82F6"
          strokeWidth="2"
          fill="none"
          initial={{ scale: 0, opacity: 0 }}
          animate={{ scale: 1, opacity: 0.6 }}
          transition={{
            duration: 1,
            delay: index * 0.3,
            repeat: Infinity,
            repeatDelay: 2
          }}
        />
      ))}
      
      {/* Static radar sweep */}
      <motion.line
        x1="200"
        y1="150"
        x2="200"
        y2="10"
        stroke="#10B981"
        strokeWidth="3"
        style={{ transformOrigin: "200px 150px" }}
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{
          duration: 1
        }}
      />
      
      {/* Threat indicators */}
      {[
        { x: 160, y: 100, type: "warning" },
        { x: 280, y: 120, type: "danger" },
        { x: 220, y: 200, type: "safe" }
      ].map((threat, index) => (
        <motion.g key={index}>
          <motion.circle
            cx={threat.x}
            cy={threat.y}
            r="8"
            fill={threat.type === "danger" ? "#EF4444" : threat.type === "warning" ? "#F59E0B" : "#10B981"}
            initial={{ scale: 0 }}
            animate={{ scale: [0, 1.2, 1] }}
            transition={{
              duration: 0.8,
              delay: index * 0.5,
              repeat: Infinity,
              repeatDelay: 2
            }}
          />
          <motion.circle
            cx={threat.x}
            cy={threat.y}
            r="12"
            stroke={threat.type === "danger" ? "#EF4444" : threat.type === "warning" ? "#F59E0B" : "#10B981"}
            strokeWidth="2"
            fill="none"
            initial={{ scale: 0, opacity: 0 }}
            animate={{ scale: [0, 1.5, 0], opacity: [0, 1, 0] }}
            transition={{
              duration: 2,
              delay: index * 0.5,
              repeat: Infinity,
              repeatDelay: 1
            }}
          />
        </motion.g>
      ))}
    </motion.svg>
  );
};

// MEV Protection Animation
export const MEVProtectionSVG: React.FC<{ className?: string }> = ({ className }) => {
  return (
    <motion.svg
      className={className}
      viewBox="0 0 400 300"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ duration: 1 }}
    >
      {/* Shield outline */}
      <motion.path
        d="M200 50 L160 80 L160 180 C160 220 180 240 200 250 C220 240 240 220 240 180 L240 80 Z"
        stroke="#3B82F6"
        strokeWidth="4"
        fill="none"
        initial={{ pathLength: 0 }}
        animate={{ pathLength: 1 }}
        transition={{ duration: 2 }}
      />
      
      {/* Shield fill */}
      <motion.path
        d="M200 60 L170 85 L170 175 C170 210 185 225 200 235 C215 225 230 210 230 175 L230 85 Z"
        fill="#3B82F6"
        fillOpacity="0.2"
        initial={{ scale: 0 }}
        animate={{ scale: 1 }}
        transition={{ duration: 1.5, delay: 0.5 }}
      />
      
      {/* MEV text */}
      <motion.text
        x="200"
        y="155"
        textAnchor="middle"
        fill="#1F2937"
        fontSize="24"
        fontWeight="bold"
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ duration: 0.8, delay: 1 }}
      >
        MEV
      </motion.text>
      
      {/* Protection pulses */}
      {[0, 1, 2].map((index) => (
        <motion.path
          key={index}
          d="M200 40 L150 75 L150 185 C150 230 170 255 200 270 C230 255 250 230 250 185 L250 75 Z"
          stroke="#10B981"
          strokeWidth="2"
          fill="none"
          initial={{ scale: 1, opacity: 0 }}
          animate={{ scale: [1, 1.2, 1.4], opacity: [0.8, 0.4, 0] }}
          transition={{
            duration: 2,
            delay: index * 0.7,
            repeat: Infinity,
            repeatDelay: 2
          }}
        />
      ))}
      
      {/* Attack arrows being deflected */}
      {[
        { startX: 50, startY: 120, endX: 150, endY: 140 },
        { startX: 350, startY: 160, endX: 250, endY: 170 },
        { startX: 80, startY: 200, endX: 160, endY: 190 }
      ].map((arrow, index) => (
        <motion.g key={index}>
          <motion.line
            x1={arrow.startX}
            y1={arrow.startY}
            x2={arrow.endX}
            y2={arrow.endY}
            stroke="#EF4444"
            strokeWidth="3"
            markerEnd="url(#arrowhead)"
            initial={{ pathLength: 0 }}
            animate={{ pathLength: [0, 1, 0] }}
            transition={{
              duration: 1.5,
              delay: index * 0.5,
              repeat: Infinity,
              repeatDelay: 3
            }}
          />
        </motion.g>
      ))}
      
      {/* Arrow marker definition */}
      <defs>
        <marker
          id="arrowhead"
          markerWidth="10"
          markerHeight="7"
          refX="10"
          refY="3.5"
          orient="auto"
        >
          <polygon
            points="0 0, 10 3.5, 0 7"
            fill="#EF4444"
          />
        </marker>
      </defs>
    </motion.svg>
  );
};

// Data Flow Animation
export const DataFlowSVG: React.FC<{ className?: string }> = ({ className }) => {
  return (
    <motion.svg
      className={className}
      viewBox="0 0 400 200"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ duration: 1 }}
    >
      {/* Processing nodes */}
      {[
        { x: 80, y: 100, label: "Input" },
        { x: 200, y: 100, label: "Process" },
        { x: 320, y: 100, label: "Output" }
      ].map((node, index) => (
        <motion.g key={index}>
          <motion.rect
            x={node.x - 30}
            y={node.y - 20}
            width="60"
            height="40"
            rx="8"
            fill="#3B82F6"
            initial={{ scale: 0 }}
            animate={{ scale: [0, 1.1, 1] }}
            transition={{ duration: 0.8, delay: index * 0.3 }}
          />
          <motion.text
            x={node.x}
            y={node.y + 5}
            textAnchor="middle"
            fill="white"
            fontSize="12"
            fontWeight="bold"
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            transition={{ duration: 0.5, delay: index * 0.3 + 0.5 }}
          >
            {node.label}
          </motion.text>
        </motion.g>
      ))}
      
      {/* Flow arrows */}
      {[
        { x1: 110, y1: 100, x2: 170, y2: 100 },
        { x1: 230, y1: 100, x2: 290, y2: 100 }
      ].map((arrow, index) => (
        <motion.line
          key={index}
          x1={arrow.x1}
          y1={arrow.y1}
          x2={arrow.x2}
          y2={arrow.y2}
          stroke="#10B981"
          strokeWidth="3"
          markerEnd="url(#dataArrow)"
          initial={{ pathLength: 0 }}
          animate={{ pathLength: 1 }}
          transition={{
            duration: 1,
            delay: 1 + index * 0.5,
            repeat: Infinity,
            repeatDelay: 2
          }}
        />
      ))}
      
      {/* Data packets */}
      {[0, 1, 2, 3, 4].map((packet) => (
        <motion.circle
          key={packet}
          cx={60}
          cy={100}
          r="4"
          fill="#10B981"
          animate={{
            cx: [60, 80, 200, 320, 340],
            opacity: [0, 1, 1, 1, 0]
          }}
          transition={{
            duration: 3,
            delay: packet * 0.6,
            repeat: Infinity,
            repeatDelay: 2,
            ease: "easeInOut"
          }}
        />
      ))}
      
      {/* Static processing indicators */}
      <motion.circle
        cx="200"
        cy="100"
        r="25"
        stroke="#F59E0B"
        strokeWidth="2"
        fill="none"
        strokeDasharray="4 4"
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        style={{ transformOrigin: "200px 100px" }}
        transition={{
          duration: 1
        }}
      />
      
      {/* Arrow marker for data flow */}
      <defs>
        <marker
          id="dataArrow"
          markerWidth="8"
          markerHeight="6"
          refX="8"
          refY="3"
          orient="auto"
        >
          <polygon
            points="0 0, 8 3, 0 6"
            fill="#10B981"
          />
        </marker>
      </defs>
    </motion.svg>
  );
};